.Naturalism{
    header{
        position: relative;
        width: 100vw;
        height: 100vh;
        min-height: 550px;
        background: url("./media/background-min.jpg") no-repeat;
        background-size: cover;
        background-attachment: fixed;
        display: grid;

        img{
            position: absolute;
            top: 0;
            left: 15px;
            height: 80px;
            @media screen and (max-width: 950px) and (min-width: 501px){
                display: none;
            }

            
        }

        h3{
            color: white;
            font-size: 1.5em;
            justify-self: center;
            align-self: center;
            letter-spacing: 0.9em;
            @media screen and (max-width: 500px){
                font-size: 1em;
            }
        }



    }

    section{
        display: flex;
        justify-content: center;
        padding-bottom: 100px;
        >div{
            position: relative;
            width: 1300px;
            -webkit-box-shadow: 2px 0px 14px 9px rgba(0,0,0,0.58); 
            box-shadow: 2px 0px 14px 9px rgba(0,0,0,0.58);
            margin-top: -200px;
            z-index: 2;
            background-color: white;
            text-align: center;
            max-width: 100vw;
            .gallery-links{
                margin-top: 50px;
                display: flex;
                justify-content: center;

                @media screen and (max-width: 650px){
                    flex-direction: column;
                }
                h3{
                    letter-spacing: 0.01em;
                    color: rgb(92, 88, 88);
                    padding: 10px;
                    transition: color 300ms, background-color 300ms;
                }

                .active{
                    background-color: rgb(218, 216, 216);
                    color: black;
                }
                h3:hover{
                    cursor: pointer;
                    color: black;
                }
            }
            p{
                padding: 50px;
                text-align: justify;
                span{
                    position: relative;
                    top: 15px;
                    color: black;
                    font-size: 4em;
                    float: left;
                    margin-bottom: 20px;
                    margin-right: 10px;
                }
                @media screen and (max-width: 500px){
                    text-align: left;
                    padding: 20px;
                }
            }

            img{
                justify-self: center;
            }
        }
        

    }
}